import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ServiceService {
  private url = 'http://nodejsapp-timzhang.us-east-1.elasticbeanstalk.com/';
  constructor(private  http: HttpClient) {}

  search(name: string, method: string): any {
    return this.http.get(this.url +method+'/'+name)
      .pipe(
        tap((response: any) => {
          response.results = response.results;
            // Not filtering in the restfulServer since in-memory-web-api has somewhat restricted api
          return response;
        })
      );
  }

  /**
   *
   * @param key = either watchlist or portfolio
   * @param value = ticker
   */
  set(key: string, value): void{
    localStorage.setItem(key, JSON.stringify(value));
  }
  get(key: string): any{
    return JSON.parse(localStorage.getItem(key));
  }
  remove(key: string, va): void{
    localStorage.removeItem(key);
  }



}


